
    import Vue from "vue";
    import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'


    export default {
        components: {
            Grid,
            GridItem
        },
        data() {
            return {
                tagData: {
                    "simple_audit": "样品审批",
                    "simple_order": "样品订单",
                    "simple_dispatch": "样品分配",
                    "standard_order": "标准订单",
                    "order_audit": "订单审批",
                    "order_price": "订单价格",
                    "order_dispatch": "订单分配",
                    "order_synchro": "订单同步"
                }
            }

        },
        created: function () {
            let self = this;
            self.$http.get(g.getPhoneUrl, {
                headers: {
                    Authorization: 'Bearer:' + g.xyToke
                }
            }).then(function (res) {
                if (res.status == 200 && res.statusText == 'OK') {
                    let phone = res.data.data.phone;
                    self.getToken(phone);
                }
            });
        },
        methods: {
            onItemClick(a) {
                if (a == 1) {
                    this.$router.push({
                        name: 'orderList'
                    })
                } else {
                    this.$router.push({
                        name: 'createForm'
                    })
                }
            },
            getToken(phone) {
                let self = this;
                self.$http.get('/jhhxPlatform/getUserToken.do?username=' + phone).then(function (res) {
                    if (res.status == 200 && res.statusText == 'OK') {
                        g.token = res.data.token;
												if(res.data.token==''){
													alert("用户不存在");
												}else{
													self.getTitle();
												}
                        
                    }
                });
            },
            getTitle() {
                let self = this;
                self.$http.get('/jhhxPlatform/order/getJhhxTitleLabel.do', {
                    params: {
                        token: g.token,
                        code: 10 //参数code：2表示获得订单标签数据，10表示获得九宫格功能菜单标签数据
                    }
                }).then(function (res) {
                    if (res.status == 200 && res.statusText == 'OK') {
                        self.tagData = res.data;
                    }
                });
            }
        }
    }




