
    import Vue from "vue"
	import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

	export default {
	  components: {
	    Group,
	    Cell,
		XInput,
		Datetime,
		XTextarea,
		XButton,
	    PopupPicker
	  },
	  data () {
    	return {
    		customValue:[],//选择客户值
    		customLists: [['']],//选择客户列表
    		addressValue:[],//选择发货地址值
    		addressLists: [['']],//选择发货地址列表
    		sendValue:[],//选择发货方式值
    		sendLists: [['']],//选择发货方式列表
    		accountValue:[],//选择结算方式值
    		accountLists: [['']],//选择结算方式列表
    		ticketValue:[],//选择票制值
    		ticketLists: [['']],//选择票制列表
			shopValue:[],//商品值
    		shopLists:[['']],//商品列表
    		shopNumValue:'',//商品数量
    		shopPriceValue:'',//商品价格
    		taxValue:[],//税率值
    		taxLists:[['']],//税率列表
    		transportValue:'',//运输费
    		loadedValue:'',//装卸费
    		orderTime:'',//时间
    		orderRemark:'',//备注
			orderInfo: {}, //提交数据
			orderDetailJson: {}, //提交数据
			sale_unit_id_str:'KG',
			unitLists:[[]],//销售单位
			tagData: {
				    "order_no":"订单编号",
					"order_type_stand":"标准订单",
					"sap_order_no":"SAP订单号",
					"status_string":"状态",
					"sale_unit":"销售单位",
					"saler":"销售人员",
					"remark":"备注",
					"deliver_status_string":"发货状态",
					"currency_code":"货币编码",
					"package_qty":"包装重量",
					"dock_free":"装卸费",
					"number":"订购PQ数量",
					"price_difference":"价格差异",
					"order_type_free":"免费订单",
					"customer_product_code":"客户物料编码",
					"cust_product_code":"客户物料编码",
					"price":"含税单价",
					"ship_model":"发货模式",
					"sale_office":"销售区域",
					"shipping_address":"送达方",
					"settle_way":"结算方式",
					"material_name":"物料名称",
					"order_type":"订单类型",
					"customer_reach_date":"客户期望到货日期",
					"deliver_time":"发货时间",
					"transport_free":"运输费",
					"is_contract":"是否合同",
					"create_time":"创建时间",
					"free_reason":"免费原因",
					"detail_remark":"备注",
					"tax":"税率",
					"transport":"快递单号",
					"packing_way":"包装方式",
					"ticket_way":"票制方式",
					"is_dock_tax":"装卸费含税",
					"money":"总价",
					"sysn_time":"同步时间",
					"is_promotion":"是否促销",
					"material_id":"物料编码",
					"customer_reach_dateStr":"客户期望到货日期",
					"factory_string":"工厂",
					"customer_name":"客户名称"
			}
    	}
     },
	 created: function() {
		 let self = this;
		 let jumpData = self.$route.query.data;
		 if(jumpData){
			 jumpData = JSON.parse(jumpData);
			 self.jumpInitData(jumpData);

		 }
		 
		 self.initTag();//初始化标签数据
		 let order_type = self.$route.query.type;//订单类型
		 order_type = '标准订单'; //todo 暂时钉死为 1为免费订单2为标准订单
		 self.orderInfo.order_type = order_type;
		//  self.initSearchCustomer();
		 self.initDict('customer',self.customLists);//下拉选择客户列表
		 self.initDict('transport_way',self.sendLists);//下拉选择发货方式列表
		 self.initDict('settle_way',self.accountLists);//下拉选择结算方式列表
		 self.initDict('ticket_way',self.ticketLists);//下拉选择票制列表
		 self.initDict('tax',self.taxLists);//下拉选择票制列表
		 self.initDict('product',self.shopLists);//下拉商品列表
		 self.initDict('shipping_address',self.addressLists);//下拉商品列表
		 self.initDict('sale_unit',self.unitLists);//销售单位
	 },
     methods:{
		 /**
		  * 测试提交数据
		  */
		 submitData:function(){
			 let self = this;
			 self.initFormData();
			 let orderInfo = self.orderInfo;
			 let orderDetailJson = self.orderDetailJson;
			 let orderInfos = [];
			 orderInfos.push(orderInfo);
			 let orderDetailJsons = [];
			 orderDetailJsons.push(orderDetailJson);

			 if(!self.submitCheck()){
				 self.$vux.toast.show({
					type: "warn",
					text: '有参数为空！'
				});
				return;
			 }

			 self.$http.get('/jhhxPlatform/order/createOrder.do',{
				 params:{
					 token:g.token,
					 orderInfo: JSON.stringify(orderInfos),
					 orderDetailJson: JSON.stringify(orderDetailJsons)
				 }
			 }).then(function(res){
				 if(res.status == 200 && res.statusText == 'OK'){
					 console.log(res);
				 }
			 });
		 },
		 /**
		  * 客户查询列表数据
		  */
		 initSearchCustomer: function(){
			 let self = this;
			 self.$http.get('/jhhxPlatform/customer/findByPage.do',{
				 params:{
					 token:g.token,
					 page:'1',
					 rows:'100'
				 }
			 }).then(function(res){
				 if(res.status == 200 && res.statusText == 'OK'){
					 let customerData = res.data.rows;
					 self.customLists[0].splice(0,self.customLists[0].length);//清空数组
					 for(let i=0; i<customerData.length;i++){
						 let child = {};
						 child.name = customerData[i].customer_name;
						 child.value = customerData[i].customer_name;
						 child.data = customerData[i];
						 self.customLists[0].push(child);
					 }
				 }
			 });
		 },
		 /**
		  * 页面初始化字典数据
		  */
		 initDict: function(type,backContent){
			 let self = this;
			 self.$http.get('/jhhxPlatform/dic/getDicByType.do',{
				 params:{
					 token:g.token,
					 type:type
				 }
			 }).then(function(res){
				 if(res.status == 200 && res.statusText == 'OK'){
					 if(res.data.length != 0){
						backContent[0].splice(0,backContent[0].length);//清空数组
						for(let i = 0 ; i<res.data.length ; i++){
							let child = {};
							if(type == 'settle_way'){
								child.name = res.data[i].dicDesc;
								child.value = res.data[i].dicDesc;
								child.data = res.data[i];
							}else{
								child.name = res.data[i].dicTxt;
								child.value = res.data[i].dicTxt;
								child.data = res.data[i];
							}
							
							backContent[0].push(child);
						}
					 }
				 }
			 });
		 },
		 /**
		  * 提交检查
		  */
		  submitCheck: function(){
			  let self = this;
			  let datas = [];
			 datas.push(self.customValue[0]);
			 datas.push(self.addressValue[0]);
			 datas.push(self.sendValue[0]);
			 datas.push(self.accountValue[0]);
			 datas.push(self.ticketValue[0]);
			 datas.push(self.shopValue[0]);
			 datas.push(self.taxValue[0]);
			 datas.push(self.transportValue);
			 datas.push(self.shopNumValue);
			 for(let i = 0 ;i<datas.length ; i++){
				 if(!datas[i]){
					 return false;
				 }
			 }
			 return true;
		  },
		 /**
		  * 值改变触发的事件
		  */
		 handleChange: function(str){
			 let self = this;
			 let orderInfo = self.orderInfo;
			 let orderDetailJson = self.orderDetailJson;
			 let data;
			 switch(str){
				 case "customer": //客户
				 	data = self.customLists[0];
				 	for(let i = 0 ; i < data.length ;i++){
						 if(data[0].length != 0 && self.customValue[0] == data[i].name){
							 console.log(data[i].data.dicId);
							 self.orderInfo.customer_id = data[i].data.dicId + '';
							 let strs = self.subStr(data[i].name);
							 if(strs.length==2){
								 self.orderInfo.customer_code = strs[0];
								 self.orderInfo.customer_name = strs[1];
							 }else{
								 self.orderInfo.customer_code = data[i].data.dicId + '';
							 	 self.orderInfo.customer_name = data[i].data.dicId + '';
							 }
							 return;
						 }
					}
					 break;
				 case "address": // 地址
					data = self.addressLists[0];
				 	for(let i = 0 ; i < data.length ;i++){
						 if(self.addressValue[0] == data[i].name){
							 console.log(data[i].data.dicId);
							 self.orderInfo.shipping_address = data[i].name;
							 self.orderInfo.ship_to = data[i].data.dicId + '';
							 return;
						 }
					}
				 	 break;
				 case "send"://请选择配送方式
					data = self.sendLists[0];
				 	for(let i = 0 ; i < data.length ;i++){
						 if(self.sendValue[0] == data[i].name){
							 console.log(data[i].data.dicId);
							 self.orderInfo.ship_model_id = data[i].data.dicId + '';
							 return;
						 }
					}
					 break;
				 case "ticket": // 票制方式
				 	data = self.ticketLists[0];
				 	for(let i = 0 ; i < data.length ;i++){
						 if(self.ticketValue[0] == data[i].name){
							 console.log(data[i].data.dicId);
							 self.orderInfo.ticket_way_id = data[i].data.dicId + '';
							 return;
						 }
					}
				 	 break;
				 case "shop": //选择商品
					data = self.shopLists[0];
				 	for(let i = 0 ; i < data.length ;i++){
						 if(self.shopValue[0] == data[i].name){
							 console.log(data[i].data.dicId);
							 self.orderDetailJson.product_id = data[i].data.dicId + '';
							 self.orderDetailJson.material_id = data[i].data.material_id;
							 self.orderDetailJson.material_name = data[i].data.material_name;
							 self.orderDetailJson.customer_product_id = data[i].data.material_id;
							 self.orderDetailJson.customer_product_code = data[i].data.material_name;
							 let units = self.unitLists[0];
							 for(let j = 0; j < units.length; j++){
								 if(data[i].data.sale_unit == units[j].name){
									 self.orderDetailJson.sale_unit_id = units[j].data.dicId + '';
									 self.sale_unit_id_str = units[j].data.dicTxt; //页面显示的汉字单位
								 }
							 }
							 return;
						 }
					}
				 	 break;
				 case "tax": // 税率
				 	data = self.taxLists[0];
				 	for(let i = 0 ; i < data.length ;i++){
						 if(self.taxValue[0] == data[i].name){
							 console.log(data[i].data.dicId);
							 self.orderDetailJson.is_dock_tax = data[i].name;
							 self.orderDetailJson.tax_id = data[i].data.dicId + '';
							 return;
						 }
					}
				 	 break;
				case "account": //结算
				 	data = self.accountLists[0];
				 	for(let i = 0 ; i < data.length ;i++){
						 if(self.accountValue[0] == data[i].name){
							 console.log(data[i].data.dicId);
							 self.orderInfo.settle_way_id = data[i].data.dicId + '';
							 return;
						 }
					}
				 	 break;
				 default:
				 	break;
			 }
		 },
		 /**
		  * 分割客户名称
		  */
		 subStr: function(str){
			 let index = str.indexOf(')');
			 let strs = [];
			 if(index == -1){
				 return strs.push(str);
			 }
			 strs.push(str.substring(0,index+1));
			 strs.push(str.substring(index+1,str.length));
			 return strs;
		 },
		 /**
		  * 跳转代入初始化数据
		  */
		jumpInitData: function(data){
			let self = this;
			//  界面的显示的数据
			self.customValue[0] = data.customer_code + data.customer_name; //客户
			self.addressValue[0] = data.shipping_address; //地址
			self.sendValue[0] = data.ship_model; //发货方式
			self.accountValue[0] = data.settle_way; //结算方式
			self.ticketValue[0] = data.ticket_way; //票制
			self.shopValue[0] = data.material_name; //商品名称
			self.shopNumValue = data.number; //数量
			self.sale_unit_id_str = data.sale_unit; //单位
			self.shopPriceValue = data.price; //单价
			self.taxValue[0] = data.tax; //税率
			self.transportValue = data.transport_free; //运输费
			self.loadedValue = data.dock_free; //装卸费
			self.orderTime = data.customer_reach_dateStr; //客户期望到达时间
			self.orderRemark = data.detail_remark; //备注

			//提交的数据
			let orderInfo = self.orderInfo;
			let orderDetailJson = self.orderDetailJson;

			 orderInfo.ship_model_id = data.ship_model_id; //发货方式Id *
			 orderInfo.settle_way_id = data.settle_way_id; //结算方式Id *
			 orderInfo.ticket_way_id = data.ticket_way_id; //票制方式Id *
			 orderInfo.customer_id = data.customer_id; //客户id *
			 orderInfo.customer_code = data.customer_code; //客户编码
			 orderInfo.customer_name = data.customer_name; //客户名称 *
			 orderInfo.shipping_address = data.shipping_address; //发货地址 *
			 orderInfo.ship_to = data.ship_to; //发货客户 *
			 orderInfo.order_type = data.order_type; //订单类型（标准订单或者免费订单）* 

			
			 orderDetailJson.product_id = data.product_id;//产品Id *
			 orderDetailJson.material_id = data.material_id;//物料编号 *
			 orderDetailJson.material_name = data.material_name;//物料名称 *
			 orderDetailJson.tax_id = data.tax_id;//税率id
			 orderDetailJson.is_dock_tax = data.is_dock_tax;//装卸费用是否含税
			 orderDetailJson.sale_unit_id = data.sale_unit_id;//销售单位 *

		},
		/**
		 * 初始化表单数据
		 */
		 initFormData: function(){
			 let self = this;
			 let orderInfo = self.orderInfo;
			 let orderDetailJson = self.orderDetailJson;

			 orderInfo.order_no = Date.parse(new Date()) +''; //当前毫秒数 *
			 orderInfo.saler_id = '管理员'; //销售员ID * 暂无
			 orderInfo.billing_to = ''; //开单方
			 orderInfo.status = '10'; //订单状态
			 orderInfo.currency_code = '1'; //货币编号 * 1:人民币，2:美元 [{"dicDesc":"CNY","dicTxt":"CNY","dicId":1},{"dicDesc":"USD","dicTxt":"USD","dicId":2}]
			 orderInfo.is_contract = '0'; //是否合同 * //默认为1  [{"dicDesc":"否","dicTxt":"否","dicId":0},{"dicDesc":"是","dicTxt":"是","dicId":1}]
			 orderInfo.order_language = 'cn'; //订单语言 cn en 
			 orderInfo.remark = ''; //备注
			 //  orderInfo.ship_model_id = ''; //发货方式Id *
			//  orderInfo.settle_way_id = ''; //结算方式Id *
			//  orderInfo.ticket_way_id = ''; //票制方式Id *
			//  orderInfo.customer_id = self.customValue[0]; //客户id *
			//  orderInfo.customer_code = ''; //客户编码
			//  orderInfo.customer_name = ''; //客户名称 *
			//  orderInfo.shipping_address = ''; //发货地址 *
			//  orderInfo.ship_to = ''; //发货客户 *
			//  orderInfo.order_type = '1'; //订单类型（标准订单或者免费订单）* 

			 orderDetailJson.customer_product_id = '1';//客户物料编码Id * 暂无
			 orderDetailJson.customer_product_code = '';//客户物料编码
			 orderDetailJson.packing_way_id = '1';//包装方式 * 暂无  [{"dicTxt":"袋子(25KG)","dicId":1},{"dicTxt":"旧桶(10KG)","dicId":2},{"dicTxt":"新桶(15KG)","dicId":3},{"dicTxt":"吨袋(50KG)","dicId":4}]
			 orderDetailJson.number = self.shopNumValue;//数量
			 orderDetailJson.price = self.shopPriceValue;//单价 *
			 orderDetailJson.money = Number(self.shopPriceValue) * Number(self.shopNumValue);//金额 *
			 orderDetailJson.transport_free = self.transportValue;//运输费用
			 orderDetailJson.dock_free = self.loadedValue;//装卸费用
			 orderDetailJson.customer_reach_date = self.orderTime;//客户期望到达时间 *
			 orderDetailJson.remark = self.orderRemark;//备注
			//  orderDetailJson.product_id = '';//产品Id *
			//  orderDetailJson.material_id = '';//物料编号 *
			//  orderDetailJson.material_name = '';//物料名称 *
			//  orderDetailJson.tax_id = '';//税率id
			//  orderDetailJson.is_dock_tax = '';//装卸费用是否含税
			//  orderDetailJson.sale_unit_id = '';//销售单位 *
		 },
		 /**
		  * 初始化标签数据
		  */
		 initTag: function(){
			 let self = this;
			self.$http.get('/jhhxPlatform/order/getJhhxTitleLabel.do',{
				params:{
					token: g.token,
					code: '2,3' // 参数code：2,3表示获得所有订单相关标签数据，10表示获得九宫格功能菜单标签数据
				}
			}).then(function(res){
				if(res.status == 200 && res.statusText == 'OK'){
					 self.tagData = res.data;
				}
			});
		 }

     }
}



