
import Vue from "vue";
import Scroller from 'vux/src/components/scroller/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Search from 'vux/src/components/search/index.vue'

export default {
    created() {
        let self = this;
        self.initOrder();
        var menus = [{
            "title": "添加订单",
            "icon": "plus",
            "type": 2, //1: 文字， 2：icon,
            "badge": 0, // 0不显示，大于0显示
            "id": "create"
        }];
        TOP.scm.navigation.setRight(menus, function (data) {
            //成功情况下对应的操作
            if (data.id == 'create') {
                self.$router.push({
                    name: 'createForm'
                })
            }
        });
    },
    components: {
        Group,
        Cell,
        Search,
        Scroller
    },
    data: function () {
        return {
            searchResults: [],
            searchValue: '',
            orderList: [],
            pullStatus: {
                pullupStatus: 'default',
                pulldownStatus: "default"
            },
            pullUpConfig: {
                content: '上拉加载更多',
                downContent: '松开进行加载',
                autoRefresh: false,
                upContent: '上拉加载更多',
                loadingContent: '加载中...',
                height: 35
            },
            pullDownConfig: {
                content: '下拉刷新',
                height: 1,
                autoRefresh: false,
                downContent: '松开进行加载',
                upContent: '下拉刷新',
                loadingContent: '加载中...'
            },
            current: {
                page: 1,
                rows: 10
            }
        }
    },
    methods: {
        loadOrders: function (page, callback) {
            let self = this;
            self.$vux.loading.show({
                text: '加载中...'
            });
            self.$http.get('/jhhxPlatform/order/findByPage.do', {
                params: {
                    token: g.token,
                    order_type: 2,
                    page: page,
                    rows: self.current.rows
                }
            }).then(callback).catch(function (error) {
                // 隐藏
                self.$vux.loading.hide();
            });
        },
        pageCreate: function () {

        },
        initOrder: function () {
            let self = this;
            self.loadOrders(self.current.page, function (response) {
                self.$vux.loading.hide();
                let responseOrderArray = response.data.rows;
                responseOrderArray.forEach(function (item) {
                    item.create_time = item.create_time / 1000;
                    self.orderList.push(item);
                });
                self.$refs.listScroller.donePullup();
                self.$refs.listScroller.donePulldown();
                setTimeout(function () {
                    self.$refs.listScroller.reset({
                        height: document.getElementById("orderContent").offsetHeight +
                            "px"
                    });
                }, 200);

            });
        },
        pageUp: function () {
            let self = this;
            console.log("pageUp");
            console.log(self.current.page);
            if (self.current.page - 1 == 0) {
                self.$refs.listScroller.donePullup();
                self.$refs.listScroller.donePulldown();
            } else {
                self.loadOrders(self.current.page - 1, function (response) {
                    self.$vux.loading.hide();
                    let responseOrderArray = response.data.rows;
                    responseOrderArray.forEach(function (item) {
                        item.create_time = item.create_time / 1000;
                        self.orderList.push(item);
                    });
                    if (responseOrderArray.length > 0) {
                        self.current.page = self.current.page + 1
                    }
                    self.$refs.listScroller.donePullup();
                    self.$refs.listScroller.donePulldown();
                    setTimeout(function () {
                        self.$refs.listScroller.reset({
                            height: document.getElementById("orderContent").offsetHeight +
                                "px"
                        });
                    }, 200);
                });
            }
        },
        jumpPage: function (item) {
            this.$router.push({
                path: "/jhhx/create",
                query: {
                    data: JSON.stringify(item)
                }
            });
        },
        pageDown: function () {
            let self = this;
            console.log("pageDown");
            self.loadOrders(self.current.page + 1, function (response) {
                self.$vux.loading.hide();
                let responseOrderArray = response.data.rows;
                responseOrderArray.forEach(function (item) {
                    item.create_time = item.create_time / 1000;
                    self.orderList.push(item);
                });
                if (responseOrderArray.length > 0) {
                    self.current.page = self.current.page + 1
                }
                self.$refs.listScroller.donePullup();
                self.$refs.listScroller.donePulldown();
                setTimeout(function () {
                    self.$refs.listScroller.reset({
                        height: document.getElementById("orderContent").offsetHeight +
                            "px"
                    });
                }, 200);
            });
        },
        getResult(val) {
            this.results = val ? getResult(this.value) : []
        }
    }
}

function getResult(val) {
    let rs = []
    for (let i = 0; i < 8; i++) {
        rs.push({
            title: `${val} result: ${i + 1} `,
            other: i
        })
    }
    return rs
}




