/**
 * 路由规则
 */
import Vue from 'vue'
import VueRouter from 'vue-router'

//佳化化学首页
import MenuList from '../pages/MenuList'

//佳化化学列表
import OrderList from '../pages/OrderList'

//佳化化学表单
import CreateForm from '../pages/CreateForm'

//无数据
import NotFound from '../pages/NotFound'


/**
 * 路由懒加载 https://router.vuejs.org/zh-cn/advanced/lazy-loading.html
 * 结合 Vue 的 异步组件 和 Webpack 的 code splitting feature, 轻松实现路由组件的懒加载。
 */
// const Login = resolve => require(['../pages/Login.vue'], resolve)

/**
 * 把组件按组分块
 * 给 chunk 命名，提供 require.ensure 第三个参数作为 chunk 的名称:
 */
// const About = r => require.ensure([], () => r(require('../pages/About.vue')), 'about')


Vue.use(VueRouter);

const routes = [{
        path: '/',
        name: 'home',
        component: MenuList, // 功能菜单
        meta: {
            title: '功能菜单'
        }
    },
    {
        path: '/jhhx/create/',
        name: 'createForm',
        component: CreateForm // 创建订单
    },
    {
        path: '/jhhx/list/',
        name: 'orderList',
        component: OrderList, // 详情列表
        meta: {
            title: '详情列表'
        }
    },
    {
        path: '*',
        name: 'notfound',
        component: NotFound
    }
]


const router = new VueRouter({
    history: true,
    base: __dirname,
    routes
})

export default router;



